import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Col, Row, Spin } from 'antd';
import BlogCard from '../UiComponents/ProfilePage/BlogCard';
import BlogCardTwo from '../UiComponents/CommonUiComponents/BlogCardTwo';
import ProjectCard from '../UiComponents/ProfilePage/ProjectCard';

const AllProjects = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get('https://ginnovation-server.herokuapp.com/api/project')
      .then((resp) => {
        console.log(resp.data[0].projects);
        setspin(false);
        setdata(resp.data[0].projects);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);
  return (
    <div className='all-blogs'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top-secondVarient project-top'>
        <header>
          <h2>Student Projects</h2>
          <p>Creative implementations of learning</p>
        </header>
      </div>
      <div className='container blog-container'>
        {data && (
          <Row gutter={8}>
            {data.map((value, index) => {
              return (
                <Col md={8} key={index}>
                  <ProjectCard
                    title={value.projectTitle}
                    subtitle={value.projectSubTitle}
                    teamName={value.teamName}
                    description={value.desc}
                    projectLink={value.projectLink}
                    abstractLink={value.abstractLink}
                  />
                </Col>
              );
            })}
          </Row>
        )}
      </div>
    </div>
  );
};

export default AllProjects;
