import React, { useEffect, useState } from 'react';
//antd imports
import { message, Spin } from 'antd';
//icons import
import { LogoutOutlined } from '@ant-design/icons';
//redux
import { useDispatch } from 'react-redux';
import { userNameChange } from '../../store';
//components import
import BioEditModal from '../UiComponents/ProfilePage/Modals/BioEditModal';
import MoreAchievementModal from '../UiComponents/ProfilePage/Modals/MoreAchievementModal';
//react-router-dom
import { Redirect } from 'react-router-dom';
//axios
import axios from 'axios';
//animation
import { motion } from 'framer-motion';
import ProfileTop from '../SectionComponents/Profile/ProfileTop';
import BioAchievement from '../SectionComponents/Profile/BioAchievement';
import Skills from '../SectionComponents/Profile/Skills';
import ProfileError from '../UiComponents/ProfilePage/ProfileError';
import LogoutConfirmation from '../UiComponents/ProfilePage/LogoutConfirmation';
import ProfileBlog from '../SectionComponents/Profile/ProfileBlog';
import ProfileProject from '../SectionComponents/Profile/ProfileProject';
import AddBlog from '../UiComponents/ProfilePage/Modals/AddBlog';
import EditBlog from '../UiComponents/ProfilePage/Modals/EditBlog';
import AddProject from '../UiComponents/ProfilePage/Modals/AddProject';
import EditAchievement from '../UiComponents/ProfilePage/Modals/EditAchievement';
import EditHeader from '../UiComponents/ProfilePage/Modals/EditHeader';
import EditProject from '../UiComponents/ProfilePage/Modals/EditProject';
import EditSkills from '../UiComponents/ProfilePage/Modals/EditSkills';
import EditDp from '../UiComponents/ProfilePage/Modals/EditDp';
//
// const { Step } = Steps;
//---------------------------------------------------------------------
const ProfilePage = () => {
  const [spin, setSpin] = useState(false);
  const [refreshKey, setRefreshKey] = useState(false);
  const [loggedIn, setLoggedIn] = useState(true);
  const [userData, setUserData] = useState();
  const [userName, setUserName] = useState();
  const [blogData, setBlogData] = useState();
  const [projectData, setProjectData] = useState();
  const [sessionActive, setSessionActive] = useState(true);

  //modals
  const [headerEdit, setHeaderEdit] = useState(false);
  const [dpEdit, setDpEdit] = useState(false);
  const [bioEdit, setBioEdit] = useState(false);
  const [achievementEdit, setAchievementEdit] = useState(false);
  const [skillEdit, setSkillEdit] = useState(false);
  const [confirmLogout, setConfirmLogout] = useState(false);
  const [moreAchievements, setMoreAchievements] = useState(false);
  const [addProject, setaddProject] = useState(false);
  const [editProject, seteditProject] = useState(false);
  const [addBlog, setAddBlog] = useState(false);
  const [editBlog, setEditBlog] = useState(false);
  //
  const dispatch = useDispatch();
  //
  // const userFullName = useSelector((state) => state.userNameReducer.userName);
  // const userName = userFullName.split(' ')[0];

  //get profile data
  useEffect(() => {
    setSpin(true);
    const token = localStorage.getItem('token');

    if (token === null) {
      setLoggedIn(false);
    } else {
      setLoggedIn(true);
    }
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/profile/data`, {
        headers: { Authorization: `Bearer ${token}` },
      })
      .then((resp) => {
        console.log(resp.data);
        setSpin(false);
        setUserData(resp.data);
        setUserName(resp.data.fullname);
        localStorage.setItem('username', resp.data.fullname);
        dispatch(userNameChange(resp.data.fullname));
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        err.response &&
          err.response.data.status === 'invalid' &&
          setSessionActive(false);
      });
  }, [refreshKey]);

  //get blogs and projects
  useEffect(() => {
    setSpin(true);
    const uid = localStorage.getItem('uid');
    axios
      .all([
        axios.get(`https://ginnovation-server.herokuapp.com/api/blog/${uid}`),
        axios.get(
          `https://ginnovation-server.herokuapp.com/api/project/${uid}`
        ),
      ])
      .then(
        axios.spread((resp1, resp2) => {
          setSpin(false);
          // console.log('Get Blog Data: ', resp1.data);
          // console.log('Get Project Data: ', resp2.data);
          setBlogData(resp1.data);
          setProjectData(resp2.data);
        })
      )
      .catch((err) => {
        setSpin(false);
        message.error("There's a error fetching blog and project data");
      });
  }, [refreshKey]);

  const userMailAddress = userData ? `mailto:${userData.email}` : '';

  return (
    <div>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      {loggedIn ? (
        <div className='profile-page'>
          {userData && blogData && projectData && sessionActive && (
            <>
              <ProfileTop
                data={userData}
                projects={projectData.projects}
                mail={userMailAddress}
                openModal={() => {
                  setHeaderEdit(true);
                }}
                dpEdit={() => {
                  setDpEdit(true);
                }}
              />
              <BioAchievement
                data={userData}
                openBio={() => {
                  setBioEdit(true);
                }}
                openAchievement={() => {
                  setAchievementEdit(true);
                }}
                moreAchievements={() => {
                  setMoreAchievements(true);
                }}
              />
              <Skills
                data={userData}
                username={userName}
                openModal={() => {
                  setSkillEdit(true);
                }}
              />
              <ProfileProject
                username={userName}
                data={projectData}
                addProject={() => {
                  setaddProject(true);
                }}
                editProject={() => {
                  seteditProject(true);
                }}
              />

              <ProfileBlog
                username={userName}
                data={blogData}
                addBlog={() => setAddBlog(true)}
                editBlog={() => setEditBlog(true)}
              />

              {/* ----------------------------------------- */}

              {moreAchievements && (
                <MoreAchievementModal
                  data={userData.achievements}
                  handleClose={() => {
                    setMoreAchievements(false);
                  }}
                />
              )}
              {headerEdit && (
                <EditHeader
                  data={userData}
                  handleClose={(changed) => {
                    setHeaderEdit(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {dpEdit && (
                <EditDp
                  data={userData}
                  handleClose={(changed) => {
                    setDpEdit(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {bioEdit && (
                <BioEditModal
                  data={userData}
                  handleClose={(changed) => {
                    setBioEdit(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {achievementEdit && (
                <EditAchievement
                  data={userData}
                  handleClose={(changed) => {
                    setAchievementEdit(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {skillEdit && (
                <EditSkills
                  data={userData}
                  handleClose={(changed) => {
                    setSkillEdit(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {addProject && (
                <AddProject
                  email={userMailAddress}
                  data={projectData.projects}
                  handleClose={(changed) => {
                    setaddProject(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {editProject && (
                <EditProject
                  data={projectData.projects}
                  handleClose={(changed) => {
                    seteditProject(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {addBlog && (
                <AddBlog
                  email={userMailAddress}
                  data={blogData}
                  handleClose={(changed) => {
                    setAddBlog(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
              {editBlog && (
                <EditBlog
                  data={blogData.blogs}
                  handleClose={(changed) => {
                    setEditBlog(false);
                    changed && setRefreshKey(refreshKey + 1);
                  }}
                />
              )}
            </>
          )}
          {sessionActive ? '' : <ProfileError />}
          {confirmLogout && (
            <LogoutConfirmation
              openModal={(value) => {
                setConfirmLogout(value);
              }}
            />
          )}
          {sessionActive && (
            <ul className='more-options'>
              <motion.li
                initial={{ x: 300 }}
                animate={{ x: 0 }}
                transition={{ delay: 0.5 }}
                className='logout'
                onClick={() => {
                  setConfirmLogout(true);
                }}
              >
                <LogoutOutlined />
              </motion.li>
            </ul>
          )}
        </div>
      ) : (
        <Redirect to='/' />
      )}
    </div>
  );
};
export default ProfilePage;
