import React from 'react';
import Avatar from 'antd/lib/avatar/avatar';
//-----------------------------------------------------------------
const ProjectCard = (props) => {
  const link = props.projectLink;
  const embedLink = [link.slice(0, 13), 'be.com/embed/', link.slice(17)].join(
    ''
  );
  const description = props.description.slice(0, 100) + '...';
  return (
    <div className='video-cards'>
      <div className='card-info'>
        <div className='youtube-video-container'>
          <iframe
            width='560'
            height='315'
            src={embedLink}
            frameBorder='0'
            allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
          ></iframe>
        </div>
        <div className='name'>
          <Avatar
            className='avatar'
            style={{ color: '#f56a00', backgroundColor: '#fde3cf' }}
          >
            P
          </Avatar>
          <h6>{props.teamName}</h6>
        </div>
        <h5>{props.title}</h5>
        <p className='subtitle'>{props.subtitle}</p>
        <p>{description}</p>
        <a href={props.abstractLink} target='_blank'>
          <button>See Project Abstract</button>
        </a>
      </div>
    </div>
  );
};
export default ProjectCard;
