import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Spin, message } from 'antd';
import { EditOutlined, CloseOutlined } from '@ant-design/icons';
//axios
import axios from 'axios';
//
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 24 },
};

//-----------------------------------------------------------------------
const EditDp = (props) => {
  const [spin, setSpin] = useState(false);
  const [imageData, setImageData] = useState('');
  const [dataChanged, setDataChanged] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  //
  const handleImage = async (e) => {
    console.log(e.target.files[0]);
    const file = e.target.files[0];
    const base64 = await convertBase64(file);
    console.log(base64);
    setImageData(base64);
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  const onFinish = (values) => {
    console.log(values);
    setSpin(true);
    setDataChanged(true);
    let formData = new FormData();
    formData.append('profilePicture', imageData);
    const updatedData = {
      profilePicture: imageData,
    };
    const config = {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'multipart/form-data',
      },
    };
    console.log('data going to upload', updatedData);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/profile/data`,
        formData,
        config.headers
      )
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.warning('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('Ouch! An error ocured');
      });
  };
  //
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Edit Your Bio <EditOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>

        <div className='form-container'>
          <Form
            {...layout}
            form={form}
            name='control-hooks'
            onFinish={onFinish}
            initialValues={{
              bio: props.data.biodata,
            }}
          >
            <input
              name='image'
              type='file'
              onChange={(e) => {
                handleImage(e);
              }}
            />
            <img src={imageData} alt='image' style={{ width: '100px' }} />
            <Form.Item className='btn-container mt-5'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Upload
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  );
};
export default EditDp;
