import React, { useEffect, useState } from 'react';
//antd imports
import { Form, Input, Upload, Button, Row, Col, Spin, message } from 'antd';
import { FormOutlined } from '@ant-design/icons';
import { PlusOutlined, CloseOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';
//
const normFile = (e) => {
  console.log('Upload event:', e);
  if (Array.isArray(e)) {
    return e;
  }
  return e && e.fileList;
};
const AddProject = (props) => {
  const [dataChanged, setDataChanged] = useState(false);
  const [spin, setSpin] = useState(false);
  const [form] = Form.useForm();
  const usermail = props.email;
  const token = localStorage.getItem('token');

  const projects = props.data;
  console.log(projects);

  const onFinish = (values) => {
    projects.push({
      projectTitle: values.projectTitle,
      email: usermail,
      projectSubTitle: values.projectSubTitle,
      projectLink: values.projectLink,
      abstractLink: values.abstractLink,
      teamName: values.teamName,
      desc: values.desc,
    });
    const updatedData = {
      projects: projects,
    };
    console.log(updatedData);
    setSpin(true);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/project`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('An error occured!');
      });
  };
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Add a New Project <PlusOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>
        <Form form={form} name='addProject' onFinish={onFinish}>
          <Row gutter={8}>
            <Col md={12}>
              <Form.Item
                name='projectTitle'
                rules={[
                  {
                    required: true,
                    message: 'Please input the title!',
                  },
                ]}
              >
                <Input
                  placeholder='Enter Project Title'
                  showCount
                  maxLength={25}
                />
              </Form.Item>
            </Col>
            <Col md={12}>
              <Form.Item
                name='projectSubTitle'
                rules={[
                  {
                    required: true,
                    message: 'Please input the subtitle!',
                  },
                ]}
              >
                <Input
                  placeholder='Enter Project Sub-Title'
                  showCount
                  maxLength={25}
                />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='projectLink'
                rules={[
                  {
                    required: true,
                    message: 'Please input the link!',
                  },
                ]}
              >
                <Input placeholder='Enter Project Link' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='abstractLink'
                rules={[
                  {
                    required: true,
                    message: 'Please input the abstarct link!',
                  },
                ]}
              >
                <Input placeholder='Enter Abstract Link' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='teamName'
                rules={[
                  {
                    required: true,
                    message: 'Please input the teamname!',
                  },
                ]}
              >
                <Input placeholder='Enter Team name' />
              </Form.Item>
            </Col>
            <Col md={24}>
              <Form.Item
                name='desc'
                rules={[
                  {
                    required: true,
                    message: 'Please input the description!',
                  },
                ]}
              >
                <Input.TextArea placeholder='Enter About Your Project' />
              </Form.Item>
            </Col>
          </Row>

          <div className='form-btn-row'>
            <div className='register-btn-container btn-container mt-5'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Upload
              </Button>
            </div>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default AddProject;
