import React from 'react';
//router imports
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
//sass imports
import '../src/Sass/main.scss';
//aos animation
import Aos from 'aos';
import 'aos/dist/aos.css';
//components import
import Home from './Components/PageComponets/Home';
import Login from './Components/PageComponets/Login';
import ProfilePage from './Components/PageComponets/ProfilePage';
import Signup from './Components/PageComponets/Signup';
import Error from './Components/PageComponets/Error';
import AboutDevelopers from './Components/PageComponets/AboutDevelopers';
import AboutGinnovation from './Components/PageComponets/AboutGinnovation';
import AboutAlumni from './Components/PageComponets/AboutAlumni';
import ForgetPassword from './Components/PageComponets/ForgetPassword';
import ResetPassword from './Components/PageComponets/ResetPassword';
import NavBar from './Components/UiComponents/Navigation/NavBar';
import ScrollTop from './Components/Functions/ScrollTop';
import BlogDetails from './Components/PageComponets/BlogDetails';
import AllBlogs from './Components/PageComponets/AllBlogs';
import AllProjects from './Components/PageComponets/AllProjects';
function App() {
  Aos.init({ duration: 100 });
  return (
    <div className='App'>
      <Router>
        <NavBar />
        <ScrollTop>
          <Switch>
            <Route exact path='/' component={Home} />
            <Route exact path='/login' component={Login} />
            <Route exact path='/forget_password' component={ForgetPassword} />
            <Route
              exact
              path='/reset_password/oJt6x4p$dueb84Yo9'
              component={ResetPassword}
            />
            <Route exact path='/signup' component={Signup} />
            <Route exact path='/profile' component={ProfilePage} />
            <Route exact path='/about/developers' component={AboutDevelopers} />
            <Route exact path='/about/alumni' component={AboutAlumni} />
            <Route exact path='/about' component={AboutGinnovation} />
            <Route exact path='/readblog' component={BlogDetails} />
            <Route exact path='/blogs' component={AllBlogs} />
            <Route exact path='/projects' component={AllProjects} />
            <Route component={Error} />
          </Switch>
        </ScrollTop>
      </Router>
    </div>
  );
}
export default App;
