import React from 'react';
import {
  EditOutlined,
  PlusCircleOutlined,
  PlusOutlined,
} from '@ant-design/icons';
import FeaturedVideoSwiper from '../../UiComponents/HomePage/FeaturedVideoSwiper';
import ProjectCard from '../../UiComponents/ProfilePage/ProjectCard';
import { Col, Row } from 'antd';

const ProfileProject = (props) => {
  const projects = props.data.projects;
  const cards = projects.map((value, index) => {
    return (
      <Col md={8} key={index}>
        <ProjectCard
          title={value.projectTitle}
          subtitle={value.projectSubTitle}
          teamName={value.teamName}
          description={value.desc}
          projectLink={value.projectLink}
          abstractLink={value.abstractLink}
        />
      </Col>
    );
  });
  return (
    <section className='projects'>
      <div className='container'>
        {projects && projects.length !== 0 ? (
          <>
            <h4 className='section-head'>Projects by {props.username}</h4>
            {projects.length > 3 ? (
              <FeaturedVideoSwiper data={projects} />
            ) : (
              <Row>{cards}</Row>
            )}
            <div className='edit-btn type2 add'>
              <PlusOutlined onClick={props.addProject} />
            </div>
            <div className='edit-btn type2'>
              <EditOutlined onClick={props.editProject} />
            </div>
          </>
        ) : (
          <div className='fresh-section' onClick={props.addProject}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Projects</p>
              <p>
                Remember, The more projects you will do, the more you will learn
              </p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileProject;
