import React, { useState } from 'react';
import { FieldTimeOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
const BlogCardTwo = (props) => {
  const [fullArticle, setfullArticle] = useState(false);
  const cardContent = props.content.slice(0, 100) + '...';
  const cardTitle = props.title.slice(0, 30) + '...';
  return (
    <div className='blog-card-two'>
      <div
        className='card-top'
        style={{
          backgroundImage:
            'url("https://i.pinimg.com/originals/32/32/a6/3232a6149c24de2b824d116b39c7df72.jpg")',
        }}
      >
        <span className='tag'>About</span>
      </div>
      <div className='card-content'>
        <div
          className='dp'
          style={{
            backgroundImage:
              'url("https://thumbs.dreamstime.com/b/face-young-handsome-bearded-indian-man-wearing-hoodie-isolated-against-white-background-169863540.jpg")',
          }}
        ></div>
        <h5>{cardTitle}</h5>
        <p className='sub-heading'>{props.subTitle}</p>
        <p className='content'>{cardContent}</p>
        <em className='author'>By Ashutosh Bisoyi</em>
        <div className='card-bottom'>
          <span>
            <FieldTimeOutlined className='icon' />6 mins ago
          </span>
          <Link
            to={{
              pathname: '/readblog',
              state: {
                title: props.title,
                subtitle: props.subTitle,
                content: props.content,
              },
            }}
          >
            <Link
              to={{
                pathname: '/readblog',
                state: {
                  title: props.title,
                  subtitle: props.subTitle,
                  content: props.content,
                },
              }}
            >
              <button>Full Article</button>
            </Link>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default BlogCardTwo;
