import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Col, Row, Spin } from 'antd';
import BlogCard from '../UiComponents/ProfilePage/BlogCard';
import BlogCardTwo from '../UiComponents/CommonUiComponents/BlogCardTwo';

const AllBlogs = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get('https://ginnovation-server.herokuapp.com/api/blog')
      .then((resp) => {
        console.log(resp.data[0].blogs);
        setspin(false);
        setdata(resp.data[0].blogs);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);
  return (
    <div className='all-blogs'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top-secondVarient'>
        <header>
          <h2>Our Blogs</h2>
          <p>Creative writtings from the creative people</p>
        </header>
      </div>
      <div className='container blog-container'>
        {data && (
          <Row gutter={8}>
            {data.map((value, index) => {
              return (
                <Col md={8} key={index}>
                  <BlogCardTwo
                    key={index}
                    title={value.blogTitle}
                    subTitle={value.subTitle}
                    content={value.blogContent}
                  />
                </Col>
              );
            })}
          </Row>
        )}
      </div>
    </div>
  );
};

export default AllBlogs;
