import { Col, Row } from 'antd';
import React from 'react';
import FeaturedVideoSwiper from '../../UiComponents/HomePage/FeaturedVideoSwiper';
import VideoCard from '../../UiComponents/HomePage/VideoCard';
//componets import
//----------------------------------------------------------------
const Videos = () => {
  return (
    <section className='videos'>
      <h2>Featured Videos</h2>
      <div className='square'></div>
      <div className='square-border'></div>
      <Row gutter={24}>
        <Col md={8}>
          <div data-aos='zoom-in' data-aos-duration='600' data-aos-delay='100'>
            <VideoCard />
          </div>
        </Col>
        <Col md={8}>
          <div data-aos='zoom-in' data-aos-duration='600' data-aos-delay='200'>
            <VideoCard />
          </div>
        </Col>
        <Col md={8}>
          <div data-aos='zoom-in' data-aos-duration='600' data-aos-delay='300'>
            <VideoCard />
          </div>
        </Col>
      </Row>
    </section>
  );
};
export default Videos;
