import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Space, Spin, message, Row, Col } from 'antd';
import {
  EditOutlined,
  CloseOutlined,
  MinusCircleOutlined,
  PlusOutlined,
} from '@ant-design/icons';
//axios
import axios from 'axios';
//-----------------------------------------------------------------------------
const EditProject = (props) => {
  // const [data, setData] = useState(props.data.projects);
  const [spin, setSpin] = useState();
  const [dataChanged, setDataChanged] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  const data = props.data;
  console.log('data at form', data);

  const onFinish = (values) => {
    const updatedData = {
      projects: values.projects,
    };
    setDataChanged(true);
    setSpin(true);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/project`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('An error occured!');
      });
  };
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Edit Projects <EditOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>

        <div className='form-container'>
          <Form
            form={form}
            initialValues={{ projects: data }}
            name='dynamic_form_nest_item'
            onFinish={onFinish}
            autoComplete='off'
          >
            <Form.List name='projects'>
              {(fields, { add, remove }) => (
                <>
                  <Row>
                    {fields.map((field) => (
                      <Col md={24}>
                        <Space key={field.key} align='baseline'>
                          <Row gutter={8} className='fields-group'>
                            <Col md={12}>
                              <Form.Item noStyle>
                                <Form.Item
                                  {...field}
                                  name={[field.name, 'projectTitle']}
                                  fieldKey={[field.fieldKey, 'projectTitle']}
                                  rules={[
                                    {
                                      required: true,
                                      message: 'Please input the title!',
                                    },
                                  ]}
                                >
                                  <Input placeholder='Enter Project Title' />
                                </Form.Item>
                              </Form.Item>
                            </Col>
                            <Col md={12}>
                              <Form.Item
                                {...field}
                                name={[field.name, 'projectSubTitle']}
                                fieldKey={[field.fieldKey, 'projectSubTitle']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the subtitle!',
                                  },
                                ]}
                              >
                                <Input placeholder='Enter Project Sub-Title' />
                              </Form.Item>
                            </Col>
                            <Col md={8}>
                              <Form.Item
                                {...field}
                                name={[field.name, 'projectLink']}
                                fieldKey={[field.fieldKey, 'projectLink']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the link!',
                                  },
                                ]}
                              >
                                <Input placeholder='Enter Youtube Link' />
                              </Form.Item>
                            </Col>
                            <Col md={8}>
                              <Form.Item
                                {...field}
                                name={[field.name, 'abstractLink']}
                                fieldKey={[field.fieldKey, 'abstractLink']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the abstract link!',
                                  },
                                ]}
                              >
                                <Input placeholder='Enter Abstract Link' />
                              </Form.Item>
                            </Col>
                            <Col md={8}>
                              <Form.Item
                                {...field}
                                name={[field.name, 'teamName']}
                                fieldKey={[field.fieldKey, 'teamName']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the team name!',
                                  },
                                ]}
                              >
                                <Input placeholder='Enter Team Name' />
                              </Form.Item>
                            </Col>
                            <Col md={24}>
                              <Form.Item
                                {...field}
                                name={[field.name, 'desc']}
                                fieldKey={[field.fieldKey, 'desc']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the about!',
                                  },
                                ]}
                              >
                                <Input.TextArea placeholder='About Your Project' />
                              </Form.Item>
                            </Col>
                          </Row>
                          <MinusCircleOutlined
                            onClick={() => remove(field.name)}
                          />
                        </Space>
                      </Col>
                    ))}
                  </Row>

                  {/* <Form.Item>
                    <Button
                      type='dashed'
                      onClick={() => add()}
                      block
                      icon={<PlusOutlined />}
                    >
                      Add New Project
                    </Button>
                  </Form.Item> */}
                </>
              )}
            </Form.List>
            <Form.Item className='btn-container'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Save Changes
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  );
};
export default EditProject;
