import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Space, Spin, message, Row, Col } from 'antd';
import {
  EditOutlined,
  CloseOutlined,
  MinusCircleOutlined,
  PlusOutlined,
} from '@ant-design/icons';
//axios
import axios from 'axios';
//-----------------------------------------------------------------------------
const EditBlog = (props) => {
  const [spin, setSpin] = useState();
  const [dataChanged, setDataChanged] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  const data = props.data;
  console.log('blog data at form', data);

  const onFinish = (values) => {
    const updatedData = {
      blogs: values.blogs,
    };
    setDataChanged(true);
    setSpin(true);
    axios
      .put(`https://ginnovation-server.herokuapp.com/api/blog`, updatedData, {
        headers: { Authorization: `Bearer ${token}` },
      })
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('An error occured!');
      });
  };
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Edit Projects <EditOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>

        <div className='form-container'>
          <Form
            form={form}
            initialValues={{ blogs: data }}
            name='dynamic_form_nest_item'
            onFinish={onFinish}
            autoComplete='off'
          >
            <Form.List name='blogs'>
              {(fields, { add, remove }) => (
                <>
                  <Row>
                    {fields.map((field) => (
                      <Col md={24}>
                        <Space key={field.key} align='baseline'>
                          <Row>
                            <Col md={12}>
                              <Form.Item noStyle>
                                <Form.Item
                                  {...field}
                                  label='Blog Title'
                                  name={[field.name, 'blogTitle']}
                                  fieldKey={[field.fieldKey, 'blogTitle']}
                                  rules={[
                                    {
                                      required: true,
                                      message: 'Please input the title!',
                                    },
                                  ]}
                                >
                                  <Input />
                                </Form.Item>
                              </Form.Item>
                            </Col>
                            <Col md={12}>
                              <Form.Item
                                {...field}
                                label='Subtitle'
                                name={[field.name, 'subTitle']}
                                fieldKey={[field.fieldKey, 'subTitle']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the subtitle!',
                                  },
                                ]}
                              >
                                <Input />
                              </Form.Item>
                            </Col>

                            <Col md={24}>
                              <Form.Item
                                {...field}
                                label='Content'
                                name={[field.name, 'blogContent']}
                                fieldKey={[field.fieldKey, 'blogContent']}
                                rules={[
                                  {
                                    required: true,
                                    message: 'Please input the content!',
                                  },
                                ]}
                              >
                                <Input.TextArea />
                              </Form.Item>
                            </Col>
                          </Row>
                          <MinusCircleOutlined
                            onClick={() => remove(field.name)}
                          />
                        </Space>
                      </Col>
                    ))}
                  </Row>

                  <Form.Item>
                    <Button
                      type='dashed'
                      onClick={() => add()}
                      block
                      icon={<PlusOutlined />}
                    >
                      Add New Project
                    </Button>
                  </Form.Item>
                </>
              )}
            </Form.List>
            <Form.Item className='btn-container'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Save Changes
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  );
};
export default EditBlog;
