import { Col, Row } from 'antd';
import React from 'react';
//icons import
import {
  EditOutlined,
  PlusOutlined,
  PlusCircleOutlined,
} from '@ant-design/icons';
import BlogCard from '../../UiComponents/ProfilePage/BlogCard';
const ProfileBlog = (props) => {
  const blogs = props.data.blogs;

  return (
    <section className='blogs'>
      <div className='container'>
        {blogs && blogs.length !== 0 ? (
          <>
            <h4 className='section-head'>Blogs by {props.username}</h4>
            <Row gutter={12} className='blog-container'>
              {blogs.map((value, index) => {
                return (
                  <Col md={8} key={index}>
                    <BlogCard
                      key={index}
                      title={value.blogTitle}
                      subTitle={value.subTitle}
                      content={value.blogContent}
                    />
                  </Col>
                );
              })}
            </Row>
            <div className='edit-btn type2 add'>
              <PlusOutlined onClick={props.addBlog} />
            </div>
            <div className='edit-btn type2'>
              <EditOutlined onClick={props.editBlog} />
            </div>
          </>
        ) : (
          <div className='fresh-section' onClick={props.addBlog}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Blogs</p>
              <p>Share your amazing thoughts by writing down a fresh Blog</p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileBlog;
